* {
    padding: 0;
    margin: 0;
    font-family: Trebuchet MS, sans-serif;
    box-sizing: border-box;
}

body {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f0f0f0;
}

a {
    color: inherit;
    text-decoration: none;
    transition: opacity 1s;
}

a:hover {
    opacity: 0.8;
}

ul {
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h5,
p {
    margin: 0;
}

button {
    padding: 0;
    border: none;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
}
